// Styles in this file applies to both the frontend and the editor

.wp-block-aab-horizontal-accordion {
  .block-editor-inner-blocks {
    width: 100%;

    .block-editor-block-list__layout {
      width: 100%;
    }
  }

  .wp-block-aab-horizontal-accordion-item {
    height: 500px;
    min-width: 100px;
    width: 100px;
    background-color: #f0f2f5;
    padding: 10px 15px;
    border-radius: 5px;
    overflow: hidden;
    cursor: pointer;

    &, & * {
      transition-duration: 1s;
    }

    & > .aahb_accordion_head {
      overflow: hidden;

      & > .aahb_accordion_icon_wrapper {
        width: 40px;
        height: 40px;
        border-radius: 5px;
        overflow: hidden;
        display: flex;
        justify-content: center;
        align-items: center;
        color: #eee;

        & > .aahb_accordion_icon {
          width: 100%;
          height: 100%;
        }

        & > .aahb_icon_upload {
          cursor: pointer;
          display: none;
        }

        &:not(.frontend):hover {
          transition-duration: 0ms;
          background-color: #777;

          & > .aahb_accordion_icon {
            display: none;
          }

          & > .aahb_icon_upload {
            display: block;
          }
        }
      }

      & > .aahb_accordion_heading {
        opacity: 0;
        padding: 10px 0;

        & .aahb_heading, & .aahb_subheading {
          margin: 0;
          padding: 0;
          line-height: 28px;
          font-size: 16px;
          white-space: nowrap !important;
          overflow: hidden;
          text-overflow: ellipsis;
        }
      }
    }

    & > .aahb_accordion_body > .block-editor-inner-blocks > .block-editor-block-list__layout,
    & > .aahb_accordion_body {
      width: 500px;
      filter: blur(5px);
      transition-duration: 100ms;

      & .wp-block-image img[alt="aahb-trees-water"] {
        border-radius: 10px;
      }
    }

    &.active {
      width: 100%;
      overflow: auto;
      scrollbar-width: none;

      & > .aahb_accordion_head {
        & > .aahb_accordion_heading {
          opacity: 100%;
        }
      }

      & > .aahb_accordion_body > .block-editor-inner-blocks > .block-editor-block-list__layout,
      & > .aahb_accordion_body {
        filter: blur(0);
        max-width: 500px;
      }
    }
  }
}